import React, { useEffect, useState } from 'react';
import Close from './Close';

function Cartmenu(props) {
  const [getQ, setQ] = useState(0) 
  const getQty = document.getElementsByClassName('qty')
  const getPrice = document.getElementsByClassName('price')
  let [linet1, setL1] = useState(0)
  let [linet2, setL2] = useState(0)
  let [linet3, setL3] = useState(0)
  let [linet4, setL4] = useState(0)
  let [linet5, setL5] = useState(0)
  
  const checkQty = () => {
    setQ(getQ +1)
  }


  useEffect(() => {
    if (getQty[0] !== "") {
      setL1(linet1 = 0)
      setL1(linet1 + getQty[0].value * getPrice[0].textContent)
    } 
  }, [getQ, linet1])

  useEffect(() =>{
    if (getQty[1] !== "") {
      setL2(linet2 = 0)
      setL2(linet2 + getQty[1].value * getPrice[1].textContent)
    }
  }, [getQ, linet2])

  useEffect(() =>{
    if (getQty[2] !== "") {
      setL3(linet3 = 0)
      setL3(linet3 + getQty[2].value * getPrice[2].textContent)
    }
  }, [getQ, linet3])

  useEffect(() =>{
    if (getQty[3] !== "") {
      setL4(linet4 = 0)
      setL4(linet4 + getQty[3].value * getPrice[3].textContent)
    }
  }, [getQ, linet4])

  useEffect(() =>{
    if (getQty[4] !== "") {
      setL5(linet5 = 0)
      setL5(linet5 + getQty[4].value * getPrice[4].textContent)
    }
  }, [getQ, linet5])


  return(
    <div className="cartmenu">
      <Close />
      <div className="carthead">
        <ul>
          <li>Food</li>
          <li>Qty</li>
          <li>Price</li>
          <li>Total</li>
        </ul>
        <hr />
      </div>

      <div className="ullist">
        <ul className="cover">
          <li className="title"></li>
          <li><input className="qty" onChange={checkQty} name="quantity"  type="number" /></li>
          <li className="price"></li>
          <li className="linetotal">{linet1}</li>
        </ul>
        <ul className="cover">
          <li className="title"></li>
          <li><input className="qty" onChange={checkQty} name="quantity"  type="number"  /></li>
          <li className="price"></li>
          <li className="linetotal">{linet2}</li>
        </ul>
        <ul className="cover">
          <li className="title"></li>
          <li><input className="qty" onChange={checkQty} name="quantity"  type="number"  /></li>
          <li className="price"></li>
          <li className="linetotal">{linet3}</li>
        </ul>
        <ul className="cover">
          <li className="title"></li>
          <li><input className="qty" onChange={checkQty} name="quantity"  type="number" /></li>
          <li className="price"></li>
          <li className="linetotal">{linet4}</li>
        </ul>
        <ul className="cover">
          <li className="title"></li>
          <li><input className="qty" onChange={checkQty} name="quantity"  type="number" /></li>
          <li className="price"></li>
          <li className="linetotal">{linet5}</li>
        </ul>
      </div>
  


      <div id="total">
        
        <h4>Total</h4>
        <h4 id="grandtotal">&#8358; {linet1 + linet2 + linet3 + linet4 + linet5} </h4>

      </div>
    </div>
  )
}


export default Cartmenu